body {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 10%,
    rgb(4, 4, 49) 100%,
    rgb(0, 0, 0) 100%
  );
}
h1 {
  color: aliceblue;
}
.card {
  margin-top: 20px;
}
.card-image {
  width: 50%;
}
.card {
  /* Add a gradient background to create a sense of depth and transparency */
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url(https://images.pexels.com/photos/7135037/pexels-photo-7135037.jpeg?auto=compress&cs=tinysrgb&w=600);
  /* Add a shadow to give the impression that the element is floating above the page */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  /* Use the border-radius property to round the corners of the element */
  border-radius: 10px;
  /* Use the transform property to add a slight tilt to the element */
  transform: rotate(0deg);
  /* Use the transition property to smooth out the transition when hovering over the element */
  transition: all 0.3s ease;
}

/* When hovering over the element, increase the shadow and tilt slightly more */
.card:hover {
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3), 0 10px 10px rgba(0, 0, 0, 0.22);
  transform: rotate(3deg);
}

